import { type State } from '../context/_types'

export const getPromptFormFirstMessage = (state: State): string => {
  if (state.selectedTeamMate?.role === undefined) return ''
  switch (state.selectedTeamMate?.voice.lang) {
    case 'ar-SA':
      return `من الآن فصاعداً تلعب دور ${state.selectedTeamMate?.role.label}، هل يمكنك الرد علي بطريقة غير رسمية ولكن احترافية.`
    case 'cs-CZ':
      return `Od teď hraješ roli ${state.selectedTeamMate?.role.label}, mohl bys mi prosím odpovědět neformálním, ale profesionálním způsobem.`
    case 'da-DK':
      return `Fra nu af spiller du en rolle som ${state.selectedTeamMate?.role.label}, kan du venligst svare mig på en uformel men professionel måde.`
    case 'de-DE':
      return `Ab sofort spielen Sie eine Rolle ${state.selectedTeamMate?.role.label}, könnten Sie mir bitte auf eine informelle, aber professionelle Weise antworten.`
    case 'el-GR':
      return `Από εδώ και στο εξής παίζετε ένα ρόλο ${state.selectedTeamMate?.role.label}, θα μπορούσατε να μου απαντήσετε με μια ανεπίσημη αλλά επαγγελματική μέθοδο.`
    case 'fi-FI':
      return `Tästä lähtien pelaat roolia ${state.selectedTeamMate?.role.label}, voisitko vastata minulle epävirallisella mutta ammattimaisella tavalla.`
    case 'fr-CA':
    case 'fr-FR':
      return `Désormais, vous jouez un rôle ${state.selectedTeamMate?.role.label}, pourriez-vous me répondre de manière informelle mais professionnelle.`
    case 'he-IL':
      return `מעכשיו אתה משחק תפקיד ${state.selectedTeamMate?.role.label}, האם תוכל לענות לי בדרך לא רשמית אבל מקצועית.`
    case 'hi-IN':
      return `अब से आप ${state.selectedTeamMate?.role.label} की भूमिका निभा रहे हैं, क्या आप मुझे एक अनौपचारिक लेकिन पेशेवर तरीके से जवाब दे सकते हैं।`
    case 'hu-HU':
      return `Mostantól ${state.selectedTeamMate?.role.label} szerepet játszol, válaszolhatnál nekem nem hivatalos, de professzionális módon.`
    case 'id-ID':
      return `Mulai sekarang Anda memainkan peran ${state.selectedTeamMate?.role.label}, bisakah Anda menjawab saya dengan cara yang tidak resmi tetapi profesional.`
    case 'it-IT':
      return `Da ora in poi giochi un ruolo ${state.selectedTeamMate?.role.label}, potresti rispondermi in modo informale ma professionale.`
    case 'ja-JP':
      return `これからは${state.selectedTeamMate?.role.label}の役割を果たします。非公式ですがプロフェッショナルな方法でお答えいただけますか。`
    case 'ko-KR':
      return `이제부터 ${state.selectedTeamMate?.role.label} 역할을하게됩니다. 비공식적이지만 전문적인 방법으로 대답해 주시겠습니까.`
    case 'nl-BE':
    case 'nl-NL':
      return `Vanaf nu speel je een rol ${state.selectedTeamMate?.role.label}, zou je me alsjeblieft informeel maar professioneel kunnen antwoorden.`
    case 'pl-PL':
      return `Od teraz grasz rolę ${state.selectedTeamMate?.role.label}, czy możesz odpowiedzieć mi nieformalnie, ale profesjonalnie.`
    case 'pt-BR':
    case 'pt-PT':
      return `A partir de agora você desempenha um papel ${state.selectedTeamMate?.role.label}, você poderia me responder de forma informal, mas profissional.`
    case 'ro-RO':
      return `De acum înainte jucați un rol ${state.selectedTeamMate?.role.label}, ați putea să-mi răspundeți într-un mod informal, dar profesional.`
    case 'ru-RU':
      return `Отныне вы играете роль ${state.selectedTeamMate?.role.label}, не могли бы вы ответить мне неформально, но профессионально.`
    case 'sk-SK':
      return `Odteraz hráte rolu ${state.selectedTeamMate?.role.label}, mohli by ste mi prosím odpovedať neformálne, ale profesionálne.`
    case 'sv-SE':
      return `Från och med nu spelar du en roll som ${state.selectedTeamMate?.role.label}, kan du svara mig på ett informellt men professionellt sätt.`
    case 'th-TH':
      return `ตอนนี้คุณเล่นบทบาท ${state.selectedTeamMate?.role.label}, คุณสามารถตอบฉันได้โดยไม่เป็นทางการแต่มีความเชี่ยวชาญ`
    case 'tr-TR':
      return `Artık ${state.selectedTeamMate?.role.label} rolünü oynuyorsunuz, lütfen bana resmi olmayan ancak profesyonel bir şekilde cevap verebilir misiniz.`
    case 'uk-UA':
      return `Відтепер ви граєте роль ${state.selectedTeamMate?.role.label}, чи можете ви відповісти мені неофіційно, але професійно.`
    case 'vi-VN':
      return `Từ nay bạn đóng vai trò ${state.selectedTeamMate?.role.label}, bạn có thể trả lời tôi một cách không chính thức nhưng chuyên nghiệp.`
    case 'zh-CN':
      return `从现在开始，您扮演的角色是${state.selectedTeamMate?.role.label}，您可以以非正式但专业的方式回答我。`
    case 'zh-HK':
      return `從現在開始，您扮演的角色是${state.selectedTeamMate?.role.label}，您可以以非正式但專業的方式回答我。`
    case 'zh-TW':
      return `從現在開始，您扮演的角色是${state.selectedTeamMate?.role.label}，您可以以非正式但專業的方式回答我。`
    case 'es-AR':
    case 'es-ES':
    case 'es-MX':
      return `A partir de ahora juegas un papel ${state.selectedTeamMate?.role.label}, ¿podrías responderme de forma informal pero profesional?`
    default:
      return `From now on you play a role of ${state.selectedTeamMate?.role.label}, could you please answer me in a informal but professional way.`
  }
}
