import { type State, type DispatchArgs } from '../_types'
import {
  GET_RESPONSE,
  SET_QUESTION,
  RESET_CONVERSATION
} from '../_const'
import { logger } from '../_logger'
import Cookies from 'universal-cookie'

const reducer = (state: State, action: DispatchArgs): State => {
  // COMMON
  const newState: State = JSON.parse(JSON.stringify(state))
  if (newState.mateToCreate !== undefined && state.mateToCreate !== undefined) newState.mateToCreate = { ...newState.mateToCreate, voice: state.mateToCreate.voice }
  if (state.voiceList !== undefined) newState.voiceList = state.voiceList
  if (state.speechRecognition !== undefined) newState.speechRecognition = state.speechRecognition
  if (state.speechSynthesis !== undefined) newState.speechSynthesis = state.speechSynthesis
  if (state.utter !== undefined) newState.utter = state.utter
  if (state.selectedTeamMate !== undefined) newState.selectedTeamMate = state.selectedTeamMate
  if (state.mates !== undefined) newState.mates = Array.from(new Set(state.mates))
  if (state.question !== undefined) newState.question = state.question
  if (state.response !== undefined) newState.response = state.response
  // END COMMON
  switch (action.type) {
    case RESET_CONVERSATION:
      newState.question = undefined
      newState.response = undefined
      break
    case SET_QUESTION:
      newState.question = action.payload
      if (newState.selectedTeamMate !== undefined) {
        if (newState.selectedTeamMate.messageHistory === undefined) newState.selectedTeamMate.messageHistory = []
        newState.selectedTeamMate.messageHistory.push({ role: 'human', content: action.payload.content })
        newState.selectedTeamMate.messageHistory = Array.from(new Map(newState.selectedTeamMate.messageHistory.map((item) => [item.content, item])).values())
      }
      break
    case GET_RESPONSE:
      newState.response = action.payload
      if (newState.selectedTeamMate !== undefined) {
        if (newState.selectedTeamMate.messageHistory === undefined) newState.selectedTeamMate.messageHistory = []
        newState.selectedTeamMate.messageHistory.push({ role: 'assistant', content: action.payload.content })
        newState.selectedTeamMate.messageHistory = Array.from(new Map(newState.selectedTeamMate.messageHistory.map((item) => [item.content, item])).values())
      }
      if (process.env.REACT_APP_DEMO === 'true') {
        newState.questionCounter++
        const cookies = new Cookies()
        cookies.set('mAI-Team-questionCounter', newState.questionCounter, { path: '/' })
      }
      break
    default:
      break
  }
  logger('log', {
    reducer: 'openAi',
    action: action.type,
    oldState: state,
    newState
  })
  return newState
}

export default reducer
