import { useContext, type ReactElement } from 'react'
import withStyles from '@mui/styles/withStyles'
import { Box, TextField } from '@mui/material'
import { StateContext } from '../../context/providers/State'
import { getStyles } from '../../assets/theme/utils'
const styleClasses = getStyles.use_styles

function TeamMateInput ({ classes }: any): ReactElement {
  const { state, actionsCollection } = useContext(StateContext)
  const handleSet = (type: string, value: string): void => {
    actionsCollection.main?.setTeamMateInput({ type, value })
  }

  return (
        <Box
            component="form"
            sx={{ '& > :not(style)': { m: 1, width: '25ch' } }}
            noValidate
            autoComplete="off"
        >
            <TextField required id="standard-basic" label="Name" variant="standard" defaultValue={state.mateToCreate?.name} onChange={(e: React.ChangeEvent<HTMLInputElement>) => { handleSet('NAME', e.target.value) }} />
            <TextField required id="standard-basic" label="Surname" variant="standard" defaultValue={state.mateToCreate?.surname} onChange={(e: React.ChangeEvent<HTMLInputElement>) => { handleSet('SURNAME', e.target.value) }} />
            <TextField id="standard-basic" label="Nick Name" variant="standard" defaultValue={state.mateToCreate?.nickName} onChange={(e: React.ChangeEvent<HTMLInputElement>) => { handleSet('NICK', e.target.value) }} />
        </Box>
  )
}

export default withStyles(styleClasses)(TeamMateInput)
