import { type DispatchArgs } from './_types'

export const dispatchAndLogEvent = (
  dispatch: React.Dispatch<DispatchArgs>,
  { type, payload }: DispatchArgs
): any => {
  if (process.env.REACT_APP_DISPATCH_LOGGED === 'true') console.log('DISPATCH', { type, payload })
  dispatch({ type, payload })
}

export const logger = (type: string, message: any): any => {
  if (process.env.REACT_APP_DISPATCH_LOGGED === 'true') {
    switch (type) {
      case 'error':
        console.error('_logger >', message)
        break
      case 'warn':
        console.warn('_logger >', message)
        break
      default:
        console.log('_logger >', message)
        break
    }
  }
}
