import {
  useContext,
  useState,
  useEffect,
  type ReactElement
} from 'react'
import withStyles from '@mui/styles/withStyles'
import {
  InputLabel,
  FormControl,
  NativeSelect
} from '@mui/material'
import { StateContext } from '../../context/providers/State'
import { getStyles } from '../../assets/theme/utils'
const styleClasses = getStyles.use_styles

const VoiceSelector = ({ classes }: any): ReactElement => {
  const { state, actionsCollection } = useContext(StateContext)
  const [voice, setTeamMateVoice] = useState('0')

  useEffect(() => {
    if (actionsCollection.main !== undefined && (state.mateToCreate?.voice === undefined || state.mateToCreate?.voice !== state.voiceList[Number(voice)])) {
      actionsCollection.main.setTeamMateVoice(state.voiceList[Number(voice)])
    }
  }, [state.voiceList, voice, actionsCollection, state])

  const handleChange = (event: any): void => {
    setTeamMateVoice(event.target.value)
    if (actionsCollection.main !== undefined) {
      actionsCollection.main.setTeamMateVoice(state.voiceList[Number(event.target.value)])
    }
  }
  return (
    <>
      {state.voiceList.length !== 0
        ? (
          <FormControl variant="filled" sx={{ m: 1, minWidth: 340 }}>
            <InputLabel variant="standard" htmlFor="uncontrolled-native">
              Select a voice
            </InputLabel>
            <NativeSelect
              defaultValue={voice}
              inputProps={{
                name: 'voice',
                id: 'uncontrolled-native'
              }}
              onChange={handleChange}
            >
              {state.voiceList.map((vOpt, i) => (
                <option value={i} key={`voice-${i}`}>
                  {vOpt.lang} - {vOpt.name}
                </option>
              ))}
            </NativeSelect>
          </FormControl>
          )
        : null}
    </>
  )
}

export default withStyles(styleClasses)(VoiceSelector)
