import {
  SET_TEAM_MATE_VOICE,
  SET_TEAM_MATE_TEST_MESSAGE,
  SET_TEAM_MATE_ROLE,
  SET_TEAM_MATE_INPUT,
  CREATE_TEAM_MATE,
  DELETE_TEAM_MATE,
  SELECT_TEAM_MATE,
  GET_VOICELIST,
  GET_SPEECH_RECOGNITION,
  GET_SPEECH_SYNTESYS,
  GET_UTTER
} from '../_const'
import { type State, type DispatchArgs, type MainActions } from '../_types'
import { dispatchAndLogEvent } from '../_logger'

const actions = (
  dispatch: React.Dispatch<DispatchArgs>,
  state: State,
  props: any
): MainActions => {
  const setTeamMateVoice = (args: any): any => {
    dispatchAndLogEvent(dispatch, {
      type: SET_TEAM_MATE_VOICE,
      payload: args
    })
  }
  const setTeamMateTestVoiceMsg = (args: any): any => {
    dispatchAndLogEvent(dispatch, {
      type: SET_TEAM_MATE_TEST_MESSAGE,
      payload: args
    })
  }
  const setTeamMateRole = (args: any): any => {
    dispatchAndLogEvent(dispatch, {
      type: SET_TEAM_MATE_ROLE,
      payload: args
    })
  }
  const setTeamMateInput = (args: any): any => {
    dispatchAndLogEvent(dispatch, {
      type: SET_TEAM_MATE_INPUT,
      payload: args
    })
  }
  const createTeamMate = (args: any): any => {
    dispatchAndLogEvent(dispatch, {
      type: CREATE_TEAM_MATE,
      payload: args
    })
  }
  const deleteTeamMate = (args: any): any => {
    dispatchAndLogEvent(dispatch, {
      type: DELETE_TEAM_MATE,
      payload: args
    })
  }
  const selectTeamMate = (args: any): any => {
    dispatchAndLogEvent(dispatch, {
      type: SELECT_TEAM_MATE,
      payload: args
    })
  }
  const getVoiceList = (): any => {
    dispatchAndLogEvent(dispatch, {
      type: GET_VOICELIST,
      payload: null
    })
  }
  const getSpeechRecognition = (): any => {
    dispatchAndLogEvent(dispatch, {
      type: GET_SPEECH_RECOGNITION,
      payload: null
    })
  }
  const getSpeechSyntesys = (): any => {
    dispatchAndLogEvent(dispatch, {
      type: GET_SPEECH_SYNTESYS,
      payload: null
    })
  }
  const getUtter = (): any => {
    dispatchAndLogEvent(dispatch, {
      type: GET_UTTER,
      payload: null
    })
  }

  return {
    setTeamMateVoice,
    setTeamMateTestVoiceMsg,
    setTeamMateRole,
    setTeamMateInput,
    createTeamMate,
    deleteTeamMate,
    selectTeamMate,
    getVoiceList,
    getSpeechRecognition,
    getSpeechSyntesys,
    getUtter
  }
}

export default actions
