import { type State, type DispatchArgs, type ActionsCollections } from '../_types'
import main from './main'
import openAi from './openAi'

const actionsCollection = (
  dispatch: React.Dispatch<DispatchArgs>,
  state: State,
  props: any
): ActionsCollections => {
  return {
    main: main(dispatch, state, props),
    openAi: openAi(dispatch, state, props)
  }
}

export default actionsCollection
