import { ChatOpenAI } from 'langchain/chat_models/openai'
import { AIMessage, HumanMessage } from 'langchain/schema'
import {
  RESET_CONVERSATION,
  GET_RESPONSE,
  SET_QUESTION,
  GET_UTTER,
  GET_SPEECH_SYNTESYS
} from '../_const'
import { type State, type DispatchArgs, type OpenAiActions } from '../_types'
import { dispatchAndLogEvent, logger } from '../_logger'

const chat = new ChatOpenAI({
  temperature: 0,
  openAIApiKey: atob(process.env.REACT_APP_OPENAI_API_LIVE ?? '')
})
const actions = (
  dispatch: React.Dispatch<DispatchArgs>,
  state: State,
  props: any
): OpenAiActions => {
  const resetConversation = (): void => {
    dispatchAndLogEvent(dispatch, {
      type: RESET_CONVERSATION,
      payload: undefined
    })
  }
  const setQuestion = async (msg: string): Promise<any> => {
    const question = new HumanMessage(msg)
    dispatchAndLogEvent(dispatch, {
      type: SET_QUESTION,
      payload: question
    })
  }
  const askQuestion = async (): Promise<any> => {
    logger('log', `askQuestion, ${state.selectedTeamMate?.messageHistory?.length}`)
    if (state.selectedTeamMate?.messageHistory === undefined || state.selectedTeamMate?.messageHistory?.length === 0) {
      logger('log', 'No history')
      return
    }
    const questions = await Promise.all(
      state.selectedTeamMate.messageHistory.map(
        async (msg: any) => {
          switch (msg.role) {
            case 'human':
              return new HumanMessage(msg.content)
            default:
              return new AIMessage(msg.content)
          }
        })
    )
    await chat.predictMessages(questions).then(response => {
      dispatchAndLogEvent(dispatch, {
        type: GET_RESPONSE,
        payload: response
      })
    })
      .catch((error) => {
        console.warn('error', error)
      })
  }

  const playResponse = (): void => {
    if (state.speechSynthesis.speaking === true) {
      state.speechSynthesis.cancel()
    }
    if (state.response?.content !== '') {
      state.utter = new window.SpeechSynthesisUtterance()
      state.utter.text = state.response?.content
      state.utter.onend = function (args: any) {
        logger('log', { event: 'SpeechSynthesisUtterance.onend', args })
        dispatchAndLogEvent(dispatch, {
          type: GET_UTTER,
          payload: undefined
        })
        dispatchAndLogEvent(dispatch, {
          type: GET_SPEECH_SYNTESYS,
          payload: undefined
        })
      }
      if (state.utter.onerror === undefined) {
        state.utter.onerror = function (args: any) {
          logger('log', { event: 'SpeechSynthesisUtterance.onerror', args })
          state.speechSynthesis.resume()
        }
      }
      if (state.utter.onpause === undefined) {
        state.utter.onpause = (args: any): void => {
          logger('log', { event: 'SpeechSynthesisUtterance.onpause', args })
        }
      }
      if (state.utter.onstart === undefined) {
        state.utter.onstart = (args: any): void => {
          logger('log', { event: 'SpeechSynthesisUtterance.onstart', args })
        }
      }
      if (state.utter.onresume === undefined) {
        state.utter.onresume = (args: any): void => {
          logger('log', { event: 'SpeechSynthesisUtterance.onresume', args })
        }
      }
      if (state.utter.onmark === undefined) {
        state.utter.onmark = (args: any): void => {
          logger('log', { event: 'SpeechSynthesisUtterance.onmark', args })
        }
      }
      if (state.utter.onboundary === undefined) {
        state.utter.onboundary = (args: any): void => {
          logger('log', { event: 'SpeechSynthesisUtterance.onboundary', args })
        }
      }
      state.utter.voice = state.selectedTeamMate?.voice
      state.utter.lang = state.selectedTeamMate?.voice.lang
      state.speechSynthesis.lang = state.selectedTeamMate?.voice.lang
      state.speechSynthesis.speak(state.utter)
    }
  }

  return {
    askQuestion,
    setQuestion,
    resetConversation,
    playResponse
  }
}

export default actions
