import { useContext, useEffect, type ReactElement } from 'react'
import withStyles from '@mui/styles/withStyles'
import { Grid, Typography, Paper, Link } from '@mui/material'
import { StateContext } from '../context/providers/State'
import { getStyles } from '../assets/theme/utils'
import { CreateTeamMate, TeamMateList } from '../components'
const styleClasses = getStyles.use_styles
function Landing ({ classes }: any): ReactElement {
  const { state, actionsCollection } = useContext(StateContext)

  useEffect(() => {
    if (state.voiceList.length === 0) {
      actionsCollection.main?.getVoiceList()
    }
    if (state.speechSynthesis === undefined) {
      actionsCollection.main?.getSpeechSyntesys()
    }
    if (state.speechRecognition === undefined) {
      actionsCollection.main?.getSpeechRecognition()
    }
    if (state.utter === undefined) {
      actionsCollection.main?.getUtter()
    }
  }, [state.voiceList, state.speechSynthesis, state.speechRecognition, state.utter, actionsCollection])

  return (
    <Grid container spacing={1} direction="column">
      <Grid item className={classes.header}>
        <Typography variant="h3" color="secondary">
          {state.title}
        </Typography>
      </Grid>
      <Grid item className={classes.body}>
        <CreateTeamMate />
        <Paper elevation={3} sx={{ margin: '10px 10px 10px 0' }}>
          <Typography variant="h6" color="secondary" sx={{ marginLeft: '10px' }}>
            Your team mates
          </Typography>
          <Paper sx={{}}>
            <TeamMateList />
          </Paper>
        </Paper>
      </Grid>
      <Grid item className={classes.footer}>
        <Link href="https://www.linkedin.com/in/marcosomma/">{state.footer}</Link>
      </Grid>
    </Grid>
  )
}

export default withStyles(styleClasses)(Landing)
