import { useContext, useState, type ReactElement } from 'react'
import withStyles from '@mui/styles/withStyles'
import { Box, Card, Typography, CardContent, CardMedia, IconButton, Tooltip } from '@mui/material'
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice'
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment'
import { StateContext } from '../../context/providers/State'
import { getStyles } from '../../assets/theme/utils'
import { type MateToStore } from '../../context/_types'
import { TeamMateChat } from '..'
const styleClasses = getStyles.use_styles

function TeamMateList ({ classes }: any): ReactElement {
  const { state, actionsCollection } = useContext(StateContext)

  const [openChatModal, setOpenChatModal] = useState(false)
  const handleOpenChatModal = (): void => { setOpenChatModal(true) }
  const handleCloseChatModal = (): void => {
    setOpenChatModal(false)
    actionsCollection.main?.selectTeamMate(undefined)
    actionsCollection.main?.getUtter()
    actionsCollection.main?.getSpeechSyntesys()
    actionsCollection.main?.getSpeechRecognition()
  }
  const handleDelete = (mate: MateToStore): void => {
    const confirm = window.confirm(`Are you sure you want to fire ${mate.name} ${mate.surname}?`)
    if (confirm) actionsCollection.main?.deleteTeamMate(mate)
  }
  const handleSelect = (mate: MateToStore): void => {
    actionsCollection.main?.selectTeamMate(mate)
    handleOpenChatModal()
  }
  return (
    <Box sx={{ display: 'flex', flexDirection: 'row' }}>
        <TeamMateChat open={openChatModal} handleClose={handleCloseChatModal} />
        {state.mates?.map((mate: MateToStore) => {
          return (
                <Card key={`teamMate-${mate.index}`} className={classes.teamMateCard} style={{ background: mate.role.color }}>
                    <CardMedia
                        component="img"
                        className={classes.teamMateCardMedia}
                        image={mate.img}
                        alt="your new team mate"
                    />
                    <CardContent className={classes.teamMateCardContent} sx={{ flexDirection: 'column' }}>
                        <Typography variant="h6" color="primary">
                            {mate.name}
                        </Typography>
                        <Typography variant="body1" color="primary">
                            {mate.surname}
                        </Typography>
                        <Typography variant="body2" color="primary">
                            Role: {mate.role.label}
                        </Typography>
                        <Typography variant="body2" color="primary">
                            Lang: {mate.voice.lang}
                        </Typography>
                        <Box className={classes.teamMateCardButtonWrapper} sx={{ flexDirection: 'row' }}>
                            <Tooltip title="Chat with me!">
                                <IconButton aria-label="chat" onClick={() => { handleSelect(mate) }}>
                                    <KeyboardVoiceIcon className={classes.teamMateCardButtonIcon} color={'primary'} />
                                </IconButton>
                            </Tooltip>
                            <Tooltip title="Fire me!">
                                <IconButton aria-label="fire" onClick={() => { handleDelete(mate) }} >
                                    <LocalFireDepartmentIcon className={classes.teamMateCardButtonIcon} color={'secondary'} />
                                </IconButton>
                            </Tooltip>
                        </Box>
                    </CardContent>
                </Card>
          )
        })
        }
    </Box>
  )
}

export default withStyles(styleClasses)(TeamMateList)
