import {
  SET_TEAM_MATE_VOICE,
  SET_TEAM_MATE_TEST_MESSAGE,
  SET_TEAM_MATE_ROLE,
  SET_TEAM_MATE_INPUT,
  CREATE_TEAM_MATE,
  DELETE_TEAM_MATE,
  SELECT_TEAM_MATE,
  GET_VOICELIST,
  GET_SPEECH_RECOGNITION,
  GET_SPEECH_SYNTESYS,
  GET_UTTER
} from '../_const'
import { type State, type DispatchArgs, type MateToStore } from '../_types'
import { logger } from '../_logger'
const sortLanguages = (a: any, b: any): number => {
  if (a.lang < b.lang) {
    return -1
  }
  if (a.lang > b.lang) {
    return 1
  }
  return 0
}
const reducer = (state: State, action: DispatchArgs): State => {
  // COMMON
  const newState: State = JSON.parse(JSON.stringify(state))
  if (newState.mateToCreate !== undefined && state.mateToCreate !== undefined) newState.mateToCreate = { ...newState.mateToCreate, voice: state.mateToCreate.voice }
  if (state.voiceList !== undefined) newState.voiceList = state.voiceList
  if (state.speechRecognition !== undefined) newState.speechRecognition = state.speechRecognition
  if (state.speechSynthesis !== undefined) newState.speechSynthesis = state.speechSynthesis
  if (state.utter !== undefined) newState.utter = state.utter
  if (state.selectedTeamMate !== undefined) newState.selectedTeamMate = state.selectedTeamMate
  if (state.mates !== undefined) newState.mates = Array.from(new Set(state.mates))
  if (state.question !== undefined) newState.question = state.question
  if (state.response !== undefined) newState.response = state.response
  // END COMMON

  switch (action.type) {
    case GET_VOICELIST:
      newState.voiceList = window.speechSynthesis.getVoices().sort(sortLanguages)
      break
    case GET_SPEECH_RECOGNITION:
      newState.speechRecognition = window.SpeechRecognition !== undefined
        ? new window.SpeechRecognition()
      // eslint-disable-next-line new-cap
        : new window.webkitSpeechRecognition()
      break
    case GET_SPEECH_SYNTESYS:
      if (newState.speechSynthesis?.speaking === true) newState.speechSynthesis.cancel()
      newState.speechSynthesis = (window as any).speechSynthesis
      break
    case GET_UTTER:
      newState.utter = new window.SpeechSynthesisUtterance()
      break
    case SET_TEAM_MATE_VOICE:
      if (newState.mateToCreate !== undefined) newState.mateToCreate = { ...newState.mateToCreate, voice: action.payload }
      else newState.mateToCreate = { voice: action.payload }
      break
    case SET_TEAM_MATE_TEST_MESSAGE:
      newState.recordedText = action.payload
      break
    case SET_TEAM_MATE_ROLE:
      if (newState.mateToCreate !== undefined) newState.mateToCreate = { ...newState.mateToCreate, role: action.payload }
      else newState.mateToCreate = { role: action.payload }
      break
    case SET_TEAM_MATE_INPUT:
      if (newState.mateToCreate !== undefined) {
        switch (action.payload.type) {
          case 'NAME':
            newState.mateToCreate = { ...newState.mateToCreate, name: action.payload.value }
            break
          case 'SURNAME':
            newState.mateToCreate = { ...newState.mateToCreate, surname: action.payload.value }
            break
          case 'NICKNAME':
          default:
            newState.mateToCreate = { ...newState.mateToCreate, nickName: action.payload.value }
            break
        }
      } else {
        switch (action.payload.type) {
          case 'NAME':
            newState.mateToCreate = { name: action.payload.value }
            break
          case 'SURNAME':
            newState.mateToCreate = { surname: action.payload.value }
            break
          case 'NICKNAME':
          default:
            newState.mateToCreate = { nickName: action.payload.value }
            break
        }
      }
      break
    case CREATE_TEAM_MATE:
      if (newState.mateToCreate?.name !== undefined &&
          newState.mateToCreate.surname !== undefined &&
          newState.mateToCreate.voice !== undefined &&
          newState.mateToCreate.role !== undefined
      ) {
        if (newState.mates === undefined) newState.mates = new Array<MateToStore>()
        const newTeamMeate: MateToStore = {
          name: newState.mateToCreate.name,
          surname: newState.mateToCreate.surname,
          voice: {
            name: newState.mateToCreate.voice.name,
            lang: newState.mateToCreate.voice.lang
          },
          role: newState.mateToCreate.role,
          img: action.payload.value,
          index: newState.mates.length
        }
        if (newState.mateToCreate?.nickName !== undefined) newTeamMeate.nickName = newState.mateToCreate.nickName
        newState.mates.push(newTeamMeate)
        newState.mateToCreate = {}
      }
      break
    case DELETE_TEAM_MATE:
      newState.mates = newState.mates?.filter((mate: MateToStore) => mate.index !== action.payload.index)
      break
    case SELECT_TEAM_MATE:
      if (action.payload === undefined) {
        newState.selectedTeamMate = undefined
        break
      }
      newState.selectedTeamMate = newState.mates?.find((mate: MateToStore) => {
        return mate.index === action.payload.index
      })
      if (newState.selectedTeamMate === undefined) break
      newState.selectedTeamMate.voice = newState.voiceList?.find((voice: any) => {
        return voice.lang === newState.selectedTeamMate?.voice.lang && voice.name === newState.selectedTeamMate?.voice.name
      })
      // in case of different voice name, but same language (OS dependent)
      if (newState.selectedTeamMate.voice === undefined) {
        newState.selectedTeamMate.voice = newState.voiceList?.find((voice: any) => {
          return voice.lang === newState.selectedTeamMate?.voice.lang
        })
      }
      break
    default:
      break
  }
  logger('log', {
    reducer: 'main',
    action: action.type,
    oldState: state,
    newState
  })
  return newState
}

export default reducer
