import {
  useContext,
  type ReactElement
} from 'react'
import withStyles from '@mui/styles/withStyles'
import {
  InputLabel,
  FormControl,
  NativeSelect
} from '@mui/material'
import { StateContext } from '../../context/providers/State'
import { getStyles } from '../../assets/theme/utils'
const styleClasses = getStyles.use_styles

const RoleSelector = ({ classes }: any): ReactElement => {
  const { state, actionsCollection } = useContext(StateContext)

  const handleChange = (event: any): void => {
    if (actionsCollection.main !== undefined) {
      actionsCollection.main.setTeamMateRole(state.roleList.find(role => role.key === Number(event.target.value)))
    }
  }
  return (
    <FormControl variant="filled" sx={{ m: 1, minWidth: 340 }}>
      <InputLabel variant="standard" htmlFor="uncontrolled-native">
        Select a role
      </InputLabel>
      <NativeSelect
        defaultValue={0}
        inputProps={{
          name: 'role',
          id: 'uncontrolled-native'
        }}
        onChange={handleChange}
      >
        <option value={0} key={`role-${0}`}>
          ---- Select a role ----
        </option>
        {state.roleList.map((vOpt) => (
          <option value={vOpt.key} key={`role-${vOpt.key}`}>
            {vOpt.label}
          </option>
        ))}
      </NativeSelect>
    </FormControl>
  )
}

export default withStyles(styleClasses)(RoleSelector)
