/* eslint-disable react/react-in-jsx-scope */
import { useContext, useState, type ReactElement, useEffect } from 'react'
import ReactMarkdown from 'react-markdown'
import withStyles from '@mui/styles/withStyles'
import { Box, Card, Typography, IconButton, CardContent, CardMedia, Modal } from '@mui/material'
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice'
import TeamMate1 from '../../assets/img/1.png'
import TeamMate2 from '../../assets/img/2.png'
import TeamMate3 from '../../assets/img/3.png'
import TeamMate4 from '../../assets/img/4.png'
import TeamMate0 from '../../assets/img/5.png'
import { StateContext } from '../../context/providers/State'
import { getStyles } from '../../assets/theme/utils'
import { type OpenAiMessage } from '../../context/_types'
import { getPromptFormFirstMessage } from '../../assets/utils'
import { logger } from '../../context/_logger'

const styleClasses = getStyles.use_styles

function TeamMateChat ({ classes, handleClose, open }: any): ReactElement {
  const { state, actionsCollection } = useContext(StateContext)

  const [recording, setRecording] = useState(false)
  const [answering, setAnswering] = useState(false)
  useEffect(() => {
    if (state.response !== undefined && !answering) {
      setAnswering(true)
      actionsCollection.openAi?.playResponse()
    }
  }, [state.response])
  const handleRecording = (): void => {
    logger('log', 'recording')
    const ask = async (): Promise<any> => {
      state.speechRecognition.onspeechend = function () {
        setRecording(false)
        state.speechRecognition.stop()
      }
      state.speechRecognition.onresult = async (event: any) => {
        logger('log', {
          listener: 'speechRecognition.onresult',
          event
        })
        const inputText =
          event.results?.[0][0].transcript !== undefined
            ? event.results[0][0].transcript
            : null
        if (inputText !== '') {
          actionsCollection.openAi?.resetConversation()
          if (state.selectedTeamMate?.messageHistory === undefined || state.selectedTeamMate?.messageHistory?.length === 0) {
            await actionsCollection.openAi?.setQuestion(getPromptFormFirstMessage(state))
          }
          await actionsCollection.openAi?.setQuestion(inputText)
          setTimeout(() => {
            actionsCollection.openAi?.askQuestion().catch((error) => {
              logger('warn', error)
            })
            setAnswering(false)
          }, 5e2)
        }
      }
      state.speechRecognition.lang = state.selectedTeamMate?.voice.lang
      state.speechRecognition.start()
    }
    setRecording(true)
    // eslint-disable-next-line @typescript-eslint/no-floating-promises
    ask()
  }

  const checkDataConsistency = (): boolean => {
    return state.selectedTeamMate?.name !== undefined &&
    state.selectedTeamMate?.surname !== undefined &&
    state.selectedTeamMate?.role !== undefined &&
    state.selectedTeamMate?.voice
  }

  const getImage = (): string => {
    if (state.selectedTeamMate?.role === undefined) return TeamMate0
    switch (state.selectedTeamMate?.role.key) {
      case 1:
        return TeamMate1
      case 2:
        return TeamMate2
      case 3:
        return TeamMate3
      case 4:
        return TeamMate4
      default:
        return TeamMate0
    }
  }

  return (
    <>
    {checkDataConsistency()
      ? (
      <Modal
        open={open}
        onClose={handleClose}
      >
        <Card sx={{
          display: 'flex',
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: '80vw',
          height: '80vh',
          boxShadow: 24,
          p: 4
        }} className={classes.card}>
          <Box sx={{ display: 'flex', flexDirection: 'column' }}>
            <CardMedia
              component="img"
              sx={{ width: 200 }}
              image={getImage()}
              alt="your new team mate"
            />
            <CardContent sx={{ flex: '1 0 auto', maxWidth: 220 }}>
              <Typography variant="h6" color="primary">
                {state.selectedTeamMate?.name} {state.selectedTeamMate?.surname}
              </Typography>
              { state.selectedTeamMate?.nickName !== undefined
                ? <Typography variant="body2" color="primary">
                    {state.selectedTeamMate?.nickName}
                  </Typography>
                : null}
              <Typography variant="body1" color="primary">
                {state.selectedTeamMate?.role?.label}
              </Typography>
              <Typography variant="body2" color="primary">
                {state.selectedTeamMate?.voice?.lang}
              </Typography>
            </CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
              { state.questionCounter >= state.maxDemoQuestions
                ? (
                <Typography variant="body2" color="secondary">
                  Sorry, you exceeded the number of questions for the DEMO. Pleas contact creator Marco Somma for more info.
                </Typography>
                  )
                : (
                  <IconButton aria-label="rec" disabled={recording || state.questionCounter >= state.maxDemoQuestions} onClick={handleRecording}>
                    <KeyboardVoiceIcon sx={{ height: 38, width: 38 }} color={'secondary'} />
                  </IconButton>
                  )
                }
            </Box>
          </Box>
          <Box sx={{ display: 'flex', alignItems: 'left' }}>
            <Box sx={{ background: 'lightgreen', padding: 1, overflow: 'auto', minWidth: '32vw', margin: '0 auto' }}>
              { state.selectedTeamMate?.messageHistory !== undefined
                ? state.selectedTeamMate?.messageHistory.map((msg: OpenAiMessage, index: number) => {
                  if (index === 0) return null
                  return msg.role === 'human'
                    ? (
                      <Box key={`question-${index}`}>
                        <b> {index} - Question: </b>
                        <ReactMarkdown>{ msg.content }</ReactMarkdown>
                        <br />
                      </Box>
                      )
                    : null
                })
                : null}
            </Box>
            <Box sx={{ background: 'lightpink', padding: 1, overflow: 'auto', minWidth: '32vw', margin: '0 auto' }}>
            { state.selectedTeamMate?.messageHistory !== undefined
              ? state.selectedTeamMate?.messageHistory.map((msg: OpenAiMessage, index: number) => {
                return msg.role !== 'human'
                  ? (
                      <Box key={`answer-${index - 1}`}>
                        <b> {index - 1} - Answer: </b>
                        <ReactMarkdown>{ msg.content }</ReactMarkdown>
                        <br />
                      </Box>
                    )
                  : null
              })
              : null}
            </Box>
          </Box>
        </Card>
      </Modal>)
      : null }
    </>
  )
}

export default withStyles(styleClasses)(TeamMateChat)
