/* eslint-disable react/react-in-jsx-scope */
import { useContext, useState, type ReactElement } from 'react'
import withStyles from '@mui/styles/withStyles'
import { Box, Card, Typography, IconButton, CardContent, CardMedia, Button, Modal } from '@mui/material'
import PlayArrowIcon from '@mui/icons-material/PlayArrow'
import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice'
import KeyboardVoiceOutlinedIcon from '@mui/icons-material/KeyboardVoiceOutlined'
import TeamMate1 from '../../assets/img/1.png'
import TeamMate2 from '../../assets/img/2.png'
import TeamMate3 from '../../assets/img/3.png'
import TeamMate4 from '../../assets/img/4.png'
import TeamMate0 from '../../assets/img/5.png'
import { StateContext } from '../../context/providers/State'
import { getStyles } from '../../assets/theme/utils'
import { VoiceSelector, RoleSelector, TeamMateInput } from '..'
import { logger } from '../../context/_logger'

const styleClasses = getStyles.use_styles

function CreateTeamMate ({ classes }: any): ReactElement {
  const { state, actionsCollection } = useContext(StateContext)

  const [open, setOpen] = useState(false)
  const [openAlert, setOpenAlert] = useState(false)
  const [recording, setRecorded] = useState(false)
  const handleRecording = (): void => {
    state.speechRecognition.onspeechend = function () {
      setRecorded(false)
      state.speechRecognition.stop()
    }
    state.speechRecognition.onresult = function (event: any) {
      const inputText =
        event.results?.[0][0].transcript !== undefined
          ? event.results[0][0].transcript
          : null
      if (inputText !== '') {
        actionsCollection.main?.setTeamMateTestVoiceMsg(inputText)
      }
    }
    setRecorded(true)
    state.speechRecognition.lang = state.mateToCreate?.voice.lang
    state.speechRecognition.start()
  }
  const handlePlay = (): void => {
    if (state.speechSynthesis === undefined) {
      actionsCollection.main?.getSpeechSyntesys()
      logger('warn', { event: 'speechSynthesis not set. Setting it now.', speechSynthesis })
    }
    if (state.speechSynthesis.speaking === true) {
      state.speechSynthesis.cancel()
    }

    if (state.recordedText !== '' && state.mateToCreate?.voice !== undefined) {
      state.utter.text = state.recordedText
      state.utter.onend = function (args: any) {
        actionsCollection.main?.getSpeechSyntesys()
        actionsCollection.main?.getUtter()
      }
      state.utter.voice = state.mateToCreate?.voice
      state.utter.lang = state.mateToCreate?.voice.lang
      state.speechSynthesis.speak(state.utter)
    }
  }
  const handleOpen = (): void => {
    if (process.env.REACT_APP_DEMO === 'true') {
      if (state.mates !== undefined && state.mates.length <= 2) setOpen(true)
      else setOpenAlert(true)
    } else {
      setOpen(true)
    }
  }
  const handleClose = (): void => {
    actionsCollection.main?.getUtter()
    actionsCollection.main?.getSpeechSyntesys()
    actionsCollection.main?.getSpeechRecognition()
    setOpen(false)
  }
  const handleCloseAlert = (): void => {
    setOpenAlert(false)
  }

  const checkDataConsistency = (): boolean => {
    return state.mateToCreate?.name !== undefined &&
    state.mateToCreate?.surname !== undefined &&
    state.mateToCreate?.role !== undefined &&
    state.mateToCreate?.voice
  }
  const getImage = (): string => {
    if (state.mateToCreate?.role === undefined) return TeamMate0
    switch (state.mateToCreate?.role.key) {
      case 1:
        return TeamMate1
      case 2:
        return TeamMate2
      case 3:
        return TeamMate3
      case 4:
        return TeamMate4
      default:
        return TeamMate0
    }
  }

  return (
    <>
      <Button onClick={handleOpen}>Create Team Mate</Button>
      <Modal
        open={open}
        onClose={handleClose}
      >
        <Card sx={{
          display: 'flex',
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: 800,
          boxShadow: 24,
          p: 4
        }} className={classes.card}>
          <Box sx={{ display: 'flex', flexDirection: 'column' }}>
            <CardContent sx={{ flex: '1 0 auto' }}>
              <Typography variant="h6" color="primary">
                Create New TeamMate
              </Typography>
              <TeamMateInput />
              <RoleSelector />
              <VoiceSelector />
              <Box
                style={{
                  marginTop: 10,
                  padding: 10
                }}>
                <b>Recorded text for test:</b> <i>{state.recordedText}</i>
              </Box>
            </CardContent>
            <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
              <IconButton aria-label="play"
                disabled={(state.recordedText === undefined || state.recordedText === '') && !recording}
                onClick={handlePlay}>
                <PlayArrowIcon sx={{ height: 38, width: 38 }}
                  color={'primary'} />
              </IconButton>
              <IconButton aria-label="rec"
                disabled={recording}
                onClick={handleRecording}>
                {
                  recording
                    ? <KeyboardVoiceIcon sx={{ height: 38, width: 38 }} color={'secondary'} />
                    : <KeyboardVoiceOutlinedIcon sx={{ height: 38, width: 38 }} color={'secondary'} />
                }
              </IconButton>
            </Box>
            <Box sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }}>
              <Button variant="contained" color="primary" onClick={() => {
                actionsCollection.main?.createTeamMate({ value: getImage() })
                handleClose()
              }}
                disabled={!checkDataConsistency()}>
                Create Team Mate
              </Button>
            </Box>
          </Box>
          <CardMedia
            component="img"
            sx={{ width: 200 }}
            image={getImage()}
            alt="your new team mate"
          />
        </Card>
      </Modal>
      <Modal
        open={openAlert}
        onClose={handleCloseAlert}
      >
        <Card sx={{
          display: 'flex',
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: 500,
          boxShadow: 24,
          p: 4,
          background: 'lightcoral'
        }}>
          <Box sx={{ display: 'flex', flexDirection: 'column' }}>
            <CardContent sx={{ flex: '1 0 auto' }}>
              <Typography variant="h6" color="alert">
                Demo Restriction
              </Typography>
              <Typography variant="body1" color="alert">
                Do you have too many team mates! Please fire one to continue.
              </Typography>
            </CardContent>
          </Box>
        </Card>
      </Modal>
    </>
  )
}

export default withStyles(styleClasses)(CreateTeamMate)
